import React, {useEffect, useState} from 'react';
import { useHistory, useParams } from 'react-router-dom'
import "./Detail.scss"

function Detail(props) {
    let { id } = useParams();
    let histroy = useHistory();
    let [stock, stoctSet] = useState(true);

    useEffect(()=>{
    let alertClose = setTimeout(() => {
        stoctSet(stock=false); }, 2000);
        return ()=>{clearTimeout(alertClose)};
    }, []);//[조건]

    return (
        <div className="container">
            { stock === true ?
            <>
            <div className="my-alert">
                <p>재고가 얼마 남지 않았습니다.</p>
            </div>
            <div className="my-alert2">
                <p>재고 소진시 이벤트 종료</p>
            </div>
            </>
                : null}
            <div div="div" className="row">
                <div className="col-md-6">
                    <img src={ props.data[id].img } width="100%"/>
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{props.data[id].title}</h4>
                    <p>{props.data[id].content}</p>
                    <p className="BeforePrice">{props.data[id].price.toLocaleString()}원</p>
                    <p className="AfterPrice">{(props.data[id].price*0.8).toLocaleString()}원</p>
                    <StockInfo Stock={props.stock[id]}></StockInfo>
                    <button className="btn btn-danger" onClick={()=>{ 
                        let newArray=[...props.stock];
                        newArray[id]= --newArray[id];
                        props.stockSet(newArray);
                    }}
                        >주문하기</button>
                    <button className="btn btn-danger" onClick={()=>{histroy.push("/")}}>뒤로가기</button>
                </div>
            </div>
        </div>
    )

    function StockInfo(props){
        console.log(props)
        return(
            <p>재고 : { props.Stock }</p>
        )
    }
}

export default Detail;